  (function(d){
    'use-strict';
    
    var ready = (callback) => {
        if (document.readyState != "loading") callback();
        else document.addEventListener("DOMContentLoaded", callback);
    }
      
    ready(() => {

/* */
        const swiperPrevButtonExist = d.querySelector(".swiper-button-prev");
        const carrousel = d.querySelector(".wp-block-lfdb-slider-swiper");
        const slides = carrousel.querySelectorAll(".wp-block-lfdb-slide-swiper");

        var rect = carrousel.getBoundingClientRect();
        console.log("rect",rect)
        // const height = `calc( 100vh - ${rect.top}px)`;
        const height = `100vh`;
        carrousel.style.setProperty("min-height", "1px");
        carrousel.style.setProperty('height', height);

        slides.forEach(slide => {
            slide.style.setProperty("min-height", "none");
            slide.style.setProperty('height', height);
            const covers = slide.querySelectorAll(".wp-block-cover");
            covers.forEach(cover => {
                cover.style.setProperty("min-height", "1px");
                cover.style.setProperty('height', height);
            })
        })
        
        // console.log(rect);
        // console.log(window.innerHeight);
        // carrousel.style.setProperty('position', 'relative');
     
    });

})(document);